const Reroot = document.getElementById("root");
let ajax = new XMLHttpRequest();
const NEW_URL = "https://api.hnpwa.com/v0/news/1.json";
const CONTENT_URL = "https:api.hnpwa.com/v0/item/@id.json";

const getData = (URL) => {
  ajax.open("GET", URL, false);
  ajax.send();

  return JSON.parse(ajax.response);
};

const newsFeed = getData(NEW_URL);

const ul = document.createElement("ul");
const content = document.createElement("div");

window.addEventListener("hashchange", () => {
  // hashÎäî #, Ï¢åÌëúÏù¥Îã§.
  const id = location.hash.substring(1);

  const newsContent = getData(CONTENT_URL.replace("@id", id));
  const title = document.createElement("h1");

  title.innerHTML = newsContent.title;

  content.appendChild(title);
});

for (let i = 0; i < newsFeed.length; i++) {
  const div = document.createElement("div");
  const li = document.createElement("li");
  const a = document.createElement("a");

  div.innerHTML = `
    <li>
      <a href="#${newsFeed[i].id}">
      ${newsFeed[i].title} (${newsFeed[i].comments_count})
      </a>
    </li>
  `; //innerHTMLÏùÄ Î¨∏ÏûêÏó¥ ÏïàÏóê ÌÉúÍ∑∏(ÎßàÌÅ¨ÏóÖ Íµ¨Ï°∞)Í∞Ä Îì§Ïñ¥ÏûàÎã§Î©¥ ÌÉúÍ∑∏Î•º DOMÏúºÎ°ú Ïù∏ÏãùÏùÑ ÌïòÏó¨ ÏùΩÎäîÎã§.

  // a.href = `#${newsFeed[i].id}`;
  // a.innerHTML = `${newsFeed[i].title} (${newsFeed[i].comments_count})`;
  // li.appendChild(a);
  // console.log(div.children[i]); //Î∞∞Ïó¥Ïùò ÌòïÏãù
  ul.appendChild(div.firstElementChild);
}

Reroot.appendChild(ul);
Reroot.appendChild(content);
